  .nav-menu {
      background: linear-gradient(45deg, #000 30%, #000 90%);
      width: 250px;
      height: 100vh;
      display: flex;
      justify-content: center;
      position: fixed;
      top: 0;
      left: -100%;
      transition: 850ms;
  }
  
  .nav-menu.active {
      left: 0;
      transition: 350ms;
  }
  
  .nav-text {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 8px 0px 8px 16px;
      list-style: none;
      height: 50px;
  }
  
  .nav-text a {
      text-decoration: none;
      color: #f5f5f5;
      font-size: 16px;
      width: 95%;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0;
      border-radius: 4px;
      text-align: left;
  }
  
  .nav-text a:hover {
      background-color: #ffffff15;
  }
  
  .nav-menu-items {
      width: 100%;
      margin: -20px;
  }
  
  .navbar-toggle {
      background-color: #000;
      width: 20%;
      height: 25px;
      display: flex;
      justify-content: flex-start;
      align-items: left;
      padding: 50px 0 20px 0;
  }
  
  span {
      margin-left: 16px;
  }